import React from 'react';

import Header from './pages/Header';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import List from './taobao/List';
import Search from './taobao/search';
import My from './taobao/My';
import dta1 from './taobao/dta1';
import dta2 from './taobao/dta2';
import dta3 from './taobao/dta3';
import dta4 from './taobao/dta4';
import dta5 from './taobao/dta5';
import dta6 from './taobao/dta6';
import dta7 from './taobao/dta7';
import dta8 from './taobao/dta8';
import dta9 from './taobao/dta9';
import dta10 from './taobao/dta10';
import dta11 from './taobao/dta11';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'

const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/List' component={List}/>
			<Route path='/My' component={My}/>
			<Route path='/home/detail' component={Search} />
			<Route path='/home/img1' component={dta1} />
			<Route path='/home/img2' component={dta2} />
			<Route path='/home/img3' component={dta3} />
			<Route path='/home/img4' component={dta4} />
			<Route path='/home/img5' component={dta5} />
			<Route path='/home/img6' component={dta6} />
			<Route path='/home/img7' component={dta7} />
			<Route path='/home/img8' component={dta8} />
			<Route path='/home/img9' component={dta9} />
			<Route path='/home/img10' component={dta10} />
			<Route path='/home/img11' component={dta11} />
			<Route render={()=><Redirect to='/'/>} />
		</Switch>
		<Mytab />
	</Router>
}
export default App;

