import React from 'react'
import { Link } from 'react-router-dom'
const dta2 = () => {
    return (
        <div>
            <Link to='/Home'> <img  src={require('./assets/back.png')} alt=""/>  </Link>
            <img  src={'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1681297841,633765002&fm=26&gp=0.jpg'} style={{backgroundSize:'contain|cover',width:'90%',height:'auto',margin:'5% 5% 1% ' }} alt=""/>
            <div style={{width:'100%',float:'fixed'}}>
                <button style={{margin:'0 2%',fontSize:'20px',backgroundColor:'#F58627'}}>店铺</button>
                <button style={{margin:'0 2%',fontSize:'20px',backgroundColor:'#F58627'}}>客服</button>
                <button style={{margin:'0 2%',fontSize:'20px',backgroundColor:'#F58627'}}>收藏</button>
                <button style={{margin:'0 0%',fontSize:'20px',backgroundColor:'#F58627'}}>加入购物车</button>
                <button style={{margin:'0 2%',fontSize:'20px',backgroundColor:'#F58627'}}>立即购买</button>
            </div>
        </div>
    )
}

export default dta2